<template lang="pug">
.comp-car-engine-params
    .forms
        el-form(label-width="120px" label-position="left")
            el-form-item(label="百公里油耗(L)")
                el-input-number(controls-position="right")
            el-form-item(label="排量(L)")
                el-input-number(controls-position="right")
            el-form-item(label="邮箱容积(L)")
                el-input-number(controls-position="right")
            el-form-item(label="进气形式")
                el-select(v-model="test")
                    el-option(v-for="(item,index) in inlet" :label="item" :value="index" :key="index")
            el-form-item(label="最大扭矩(N.m)")
                el-input-number(controls-position="right")

            el-form-item(label="")
                my-button(type="primary") 保存
        el-form(label-width="120px" label-position="left")
            el-form-item(label="燃油")
                el-select(v-model="test")
                    el-option(v-for="(item,index) in fuel" :label="item" :value="index" :key="index")
            el-form-item(label="燃油标号")
                el-select(v-model="test")
                    el-option(v-for="(item,index) in fuelLabel" :label="item" :value="index" :key="index")
            el-form-item(label="功率(kw)")
                el-input-number(controls-position="right")
            el-form-item(label="最高车速(km/h)")
                el-input-number(controls-position="right")
            el-form-item(label="官方0-100加速(s)")
                el-input-number(controls-position="right")

</template>

<script>
import { inlet, fuel, fuelLabel } from '../../../lib/carConfig';
export default {
    data () {
        return {
            test: '',
            inlet,
            fuel,
            fuelLabel,
            form: {
                pf: '',
                location: ''
            }
        };
    }
};
</script>

<style lang="scss" scoped>
.comp-car-engine-params {
    .forms {
        display: flex;
        .el-form {
            &:last-child {
                padding-left: 40px;
            }
        }
    }
}
</style>



