<template lang="pug">
.comp-car-base-params
    .forms
        el-form(label-width="100px" label-position="left")
            el-form-item(label="车型")
                el-select(v-model="test" )
                    el-option(v-for="(item,index) in model" :label="item" :value="index" :key="index")
            el-form-item(label="车辆颜色")
                el-select(v-model="test")
                    el-option(v-for="(item,index) in color" :label="item" :value="index" :key="index")
            el-form-item(label="变速箱")
                el-select(v-model="test")
                    el-option(v-for="(item,index) in gearBox" :label="item" :value="index" :key="index")
            el-form-item(label="变速箱型号")
                el-input
            el-form-item(label="车门数")
                el-input-number(controls-position="right")
            el-form-item(label="座位数")
                el-input-number(controls-position="right")
            el-form-item(label="轮毂材料")
                el-select(v-model="test")
                    el-option(v-for="(item,index) in hubMaterial" :label="item" :value="index" :key="index")
            el-form-item(label="")
                my-button(type="primary") 保存
        el-form(label-width="100px" label-position="left")
            el-form-item(label="长(mm)")
                el-input-number(controls-position="right")
            el-form-item(label="宽(mm)")
                el-input-number(controls-position="right")
            el-form-item(label="高(mm)")
                el-input-number(controls-position="right")
            el-form-item(label="轴距(mm)")
                el-input-number(controls-position="right")
            el-form-item(label="行李箱容积(L)")
                el-input-number(controls-position="right")

            el-form-item(label="整备质量(kg)")
                el-input-number(controls-position="right")
</template>

<script>
import { model, color, gearBox, hubMaterial } from '../../../lib/carConfig';
export default {
    data () {
        return {
            test: '',
            model,
            color,
            gearBox,
            hubMaterial,
            form: {
                pf: '',
                location: ''
            }
        };
    }
};
</script>

<style lang="scss" scoped>
.comp-car-base-params {
    .forms {
        display: flex;
        .el-form {
            &:last-child {
                padding-left: 40px;
            }
        }
    }
}
</style>



