<template lang="pug">
.comp-car-brake-params
    el-form(label-width="120px" label-position="left")
        el-form-item(label="前制动")
            el-select(v-model="test")
                el-option(v-for="(item,index) in brake" :label="item" :value="index" :key="index")
        el-form-item(label="后制动")
            el-select(v-model="test")
                el-option(v-for="(item,index) in brake" :label="item" :value="index" :key="index")
        el-form-item.tyre(label="前轮胎规格(mm)")
            el-input-number(placeholder="宽度" controls-position="right")
            span /
            el-input-number(placeholder="扁平率" controls-position="right")
            span R
            el-input-number(placeholder="直径" controls-position="right")
        el-form-item.tyre(label="后轮胎规格(mm)")
            el-input-number(placeholder="宽度" controls-position="right")
            span /
            el-input-number(placeholder="扁平率" controls-position="right")
            span R
            el-input-number(placeholder="直径" controls-position="right")

        el-form-item(label="")
            my-button(type="primary") 保存
</template>

<script>
import { brake } from '../../../lib/carConfig';
export default {
    data () {
        return {
            test: '',
            brake
        };
    }
};
</script>

<style lang="scss" scoped>
.comp-car-brake-params {
    .tyre {
        span {
            padding: 0 10px;
        }
    }
}
</style>



