<template lang="pug">
.comp-car-desc
    el-form(label-width="120px" label-position="left")
        el-form-item(label="驱动方式")
            el-select(v-model="test")
                el-option(v-for="(item,index) in drivingModel" :label="item" :value="index" :key="index")
        el-form-item(label="转向系统")
            el-select(v-model="test")
                el-option(v-for="(item,index) in steeringSystem" :label="item" :value="index" :key="index")
        el-form-item(label="前悬挂")
            el-select(v-model="test")
                el-option(v-for="(item,index) in suspensionSystem" :label="item" :value="index" :key="index")
        el-form-item(label="后悬挂")
            el-select(v-model="test")
                el-option(v-for="(item,index) in suspensionSystem" :label="item" :value="index" :key="index")

        el-form-item(label="")
            my-button(type="primary") 保存
</template>

<script>
import { drivingModel, steeringSystem, suspensionSystem } from '../../../lib/carConfig';
export default {
    data () {
        return {
            test: '',
            drivingModel,
            steeringSystem,
            suspensionSystem
        };
    }
};
</script>



