<template lang="pug">
.comp-car-base-info
    el-form.form(label-width="100px" label-position="left")
        el-form-item(label="车商")
            el-input()
        el-form-item(label="标题")
            el-input()
        el-form-item(label="车型")
            el-cascader(:options="letterList" @active-item-change="handleItemChange" v-model="form.model")
        el-form-item(label="卖价")
            el-input-number(controls-position="right")
            span.unit 万元
        el-form-item(label="新车价")
            el-input-number(controls-position="right")
            span.unit 万元
        el-form-item(label="新车购置税")
            el-input-number(controls-position="right")
            span.unit 万元
        el-form-item(label="上牌时间")
            el-date-picker(type="month")
        el-form-item(label="行驶里程")
            el-input-number(controls-position="right")
        el-form-item(label="归属地")
            el-select(v-model="test")
                el-option(label="成都" value="1")
        el-form-item(label="排放标准")
            el-select(v-model="test")
                el-option(v-for="(item, index) in gas" :label="item" :value="index" :key="index")
    .submit
        my-button(type="primary" :click="save") 保存
</template>

<script>
import { gas } from '../../../lib/carConfig';
export default {
    data () {
        return {
            gas: gas,
            test: '',
            form: {
                pf: '',
                location: '',
                model: []
            },
            letterList: []
        };
    },
    methods: {
        async getLetterList () {
            try {
                let { data } = await this.$http.post('/admin/car/letterlist', {p: 1});
                this.letterList = data.data.list.map(item => {
                    return {
                        label: item,
                        value: {
                            type: 'letter',
                            value: item
                        },
                        children: []
                    };
                });
            } catch (error) {
                console.log(error);
            }
        },
        async handleItemChange (values) {
            console.log(arguments);
            let val = values[values.length - 1];
            if (val.type === 'letter') {
                let found = this.letterList.find(item => {
                    return item.label === val.value;
                });
                let brands = await this.getBrandList(val.value);
                found.children = brands;
            } else if (val.type === 'brand') {
                let parentLetter = this.letterList.find(item => {
                    return item.label === values[0].value;
                });
                let foundBrand = parentLetter.children.find(item => {
                    return item.label === val.value;
                });
                foundBrand.children = await this.getModelList(foundBrand);
            }
        },
        async getBrandList (letter) {
            try {
                let { data } = await this.$http.post('/admin/car/brandlist', {p: 1, letter});
                return data.data.list.map(item => {
                    return {
                        label: item.brand,
                        value: {
                            type: 'brand',
                            value: item.brand
                        },
                        children: []
                    };
                });
            } catch (error) {
                console.log(error);
                return [];
            }
        },
        async getModelList (brand) {
            try {
                let { data } = await this.$http.post('/admin/car/modellist', {'brand': brand.label});
                return data.data.list.map(item => {
                    return {
                        label: item.model,
                        value: {
                            type: 'model',
                            value: item.id
                        }
                    };
                });
            } catch (error) {
                console.log(error);
                return [];
            }
        },
        save () {
            this.$router.push({
                path: '/car/edit'
            });
            this.$emit('start-edit');
        }
    },
    mounted () {
        this.getLetterList();
    }
};
</script>

<style lang="scss" scoped>
.comp-car-base-info {
    .form {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        .el-form-item {
            box-sizing: border-box;
            width: 50%;
            padding-right: 100px;
        }
        .el-cascader {
            width: 250px;
        }
    }
}
</style>



